.checkbox3 {
  position: relative; }
  .checkbox3 input {
    position: absolute;
    left: -9999px; }
  .checkbox3 label {
    display: block;
    position: relative;
    padding: 11px 0 11px 30px;
    white-space: nowrap;
    cursor: pointer;
    font-size: 12px;
    margin-bottom: 0;
    margin-top: -4px; }
  .checkbox3 label::before {
    content: '';
    display: block;
    position: absolute;
    top: 10px;
    bottom: 10px;
    left: 0;
    width: 21px;
    height: 21px;
    border: 1px solid #CCC;
    -moz-border-radius: 1px;
         border-radius: 1px;
    -webkit-transition: background-color 0.2s;
       -moz-transition: background-color 0.2s;
            transition: background-color 0.2s;
    overflow: hidden;
    vertical-align: middle;
    text-align: center; }
  .checkbox3 label::after {
    content: '';
    display: block;
    position: absolute;
    top: 10px;
    bottom: 10px;
    left: 0;
    width: 19px;
    height: 19px;
    border: 12px solid #FFF;
    margin: 1px;
    -webkit-transition: all 0.05s;
    -moz-transition: all 0.05s;
         transition: all 0.05s;
    opacity: 0; }
  .checkbox3 input:checked + label::before {
    border-width: 1px;
    border-style: solid;
    background-color: #444;
    border-color: #444;
    color: #fff; }
  .checkbox3 input:checked + label::after {
    border: 3px solid #FFF;
    opacity: 1; }

